class DimensionChangeListener {
    observers = [];

    attach(observer) {
        this.observers.push(observer);
    }

    detach(observerToRemove) {
        this.observers = this.observers.filter(observer => observer !== observerToRemove);
    }

    notify(v) {
        this.observers.forEach(ob => ob(v));
    }
}

const dimensionChangeListener = new DimensionChangeListener();
export default dimensionChangeListener;