
import { useState, useEffect } from "react"
import dimensionChangeListener from "./index";

export const DIMENSIONS = {
    MOBILE: "MOBILE",
    DESKTOP: "DESKTOP",
}

function debounce(fn, ms) {
    let timer
    return _ => {
        clearTimeout(timer)
        timer = setTimeout(_ => {
            timer = null
            fn.apply(this, arguments)
        }, ms)
    };
}

export const DimensionChangeListenerComponent = () => {
    const [dimensions, setDimensions] = useState(null);

    useEffect(() => {
        const w = document.documentElement.clientWidth;
        const initialState = w > 728 ? DIMENSIONS.DESKTOP : DIMENSIONS.MOBILE;
        setDimensions(initialState);
        dimensionChangeListener.notify(initialState);

        const displayWindowSize = debounce(() => {
            const w = document.documentElement.clientWidth;
            const isDesktopSize = w > 728;
            if (dimensions === DIMENSIONS.DESKTOP && !isDesktopSize) {
                setDimensions(DIMENSIONS.MOBILE);
                // dimensionChangeListener.notify(DIMENSIONS.MOBILE);
            } else if (dimensions === DIMENSIONS.MOBILE && isDesktopSize) {
                setDimensions(DIMENSIONS.DESKTOP);
                // dimensionChangeListener.notify(DIMENSIONS.DESKTOP);
            }
        }, 250);

        window.addEventListener("resize", displayWindowSize);

        return () => {
            window.removeEventListener("resize", displayWindowSize);
        };
    }, [setDimensions, dimensions]);

    return null;
};

export default DimensionChangeListenerComponent;